<template>
    <div>
      <mt-header fixed title="行情"></mt-header>
      <MarketList class="marketlist" :dataList="dataList"></MarketList>
      <mt-tabbar fixed v-model="tabbarSelected">
        <mt-tab-item id="资讯" to="/">
          <span class="iconfont" slot="icon">&#xe75e;</span>
          资讯
        </mt-tab-item>
        <mt-tab-item id="行情">
          <span class="iconfont" slot="icon">&#xe757;</span>
          行情
        </mt-tab-item>
      </mt-tabbar>
    </div>
</template>

<script>
import MarketList from './MarketList'
export default {
  name: 'market',
  components: {
    MarketList
  },
  data () {
    return {
      dataList: [],
      tabbarSelected: ''
    }
  },
  watch: {
    tabbarSelected (val) {
      if (val === '资讯') {
        this.$router.push('/')
      }
    }
  },
  methods: {
    getHomeInfo () {
      this.$store.dispatch('coinListData').then(response => {
        this.dataList = response.items
      })
    }
  },
  mounted () {
    this.getHomeInfo()
  }
}
</script>

<style lang="stylus" scoped>
  .marketlist
    display: flex
    flex-direction: column
    margin-top: 1rem
    margin-bottom: 1rem
</style>



